import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';

class NavBar extends React.Component {
	constructor() {
		super();
		this.scrollRef = React.createRef();
		this.state = { scrollTop: 0 };
	}

	onScroll = () => {
		const scrollY = window.scrollY;
		const scrollTop = this.scrollRef.current.scrollTop;
		console.log(`onScroll, window.scrollY: ${scrollY} scrollRef.scrollTop: ${scrollTop}`)
		this.setState({ scrollY, scrollTop });
	}

	componentDidMount() {
		window.addEventListener('scroll', this.onScroll);
	}

	componentWillUnmount() {
		window.removeEventListener('scroll', this.onScroll);
	}

	render() {
		const { scrollY } = this.state;
		let { background, color } = { background: 'transparent', color: 'white' };
		if (window.innerWidth < 700) {
			background = 'white';
			color = 'black';
		} else if (scrollY > 0) {
			background = 'white';
			color = 'black';
		} else {
			background = 'transparent';
			color = 'white';
		}

		return (
			<Navbar ref={this.scrollRef} variant="light" expand="lg" fixed="top" style={{ background }}>
				<Navbar.Brand href="#header" style={{ fontSize: '16px', color }}>CLASHPERK</Navbar.Brand>
				<Navbar.Toggle aria-controls="basic-navbar-nav" />

				{/* Collapse Navbar */}
				<Navbar.Collapse id="basic-navbar-nav">

					{/* Left Side Menu */}
					<Nav className="mr-auto" >
						<Nav.Link style={{ color }} href="/">HOME</Nav.Link>
						<Nav.Link style={{ color }} href="#features">FEATURES</Nav.Link>
					</Nav>

					{/* Right Side Menu */}
					<Nav>
						<Nav.Link style={{ color }} href="#teams">TEAMS</Nav.Link>
						<Nav.Link style={{ color }} href="#login">LOGIN</Nav.Link>
					</Nav>
				</Navbar.Collapse>
			</Navbar>
		);
	}
}

export default NavBar;